/**
 * Clearing and floating
 */

.alignleft,
.float-left {
    float: left;
    img & {
        margin-right: $sp-unit;
    }
}

.alignright,
.float-right {
    float: right;
    img & {
        margin-left: $sp-unit;
    }
}

.aligncenter,
.float-center {
    display: block;
    margin-left: auto;
    margin-right: auto;
    img & {
        margin-bottom: $sp-unit;
    }
}

.clearfix {
    @include clearfix();
}


/**
 * Text aligments
 */

.align-text-left {
    text-align: left;
}

.align-text-right {
    text-align: right;
}

.align-text-center {
    text-align: center;
}


/**
 * Elements visibility
 */

.visible-on-mobile {
    display: none !important;
    @include resp-max($breakpoint-sm) {
        display: block !important;
    }
}

.visible-on-tablet {
    display: none !important;
    @include resp-max($breakpoint-md) {
        display: block !important;
    }
    @include resp-max($breakpoint-sm) {
        display: none !important;
    }
}

.visible-on-desktop {
    @include resp-max($breakpoint-md) {
        display: none !important;
    }
}

//Accessibly hide elements
%visuallyhidden {
    margin: -1px;
    padding: 0;
    width: 1px;
    height: 1px;
    overflow: hidden;
    clip: rect(0 0 0 0);
    clip: rect(0, 0, 0, 0);
    position: absolute;
}